:root {
  /*--input-color: #99A3BA;*/
  --input-color: #4a5651;
  --input-border: #cdd9ed;
  --input-background: #fff;
  --input-placeholder: #cbd1dc;

  --input-border-focus: #275efe;

  --group-color: var(--input-color);
  --group-border: var(--input-border);
  --group-background: #eef4ff;

  --group-color-focus: #fff;
  --group-border-focus: var(--input-border-focus);
  --group-background-focus: #678efe;
}

.default {
  min-width: 100px;
  background: var(--group-background-focus);
  border-radius: 24px;
  font-size: 18px;
  cursor: pointer;
  padding: 4px;
}
.default:hover {
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.5),
    0 0 20px rgba(255, 255, 255, 0.2);
  outline-color: rgba(255, 255, 255, 0);
  outline-offset: 15px;
  text-shadow: 1px 1px 2px #427388;
}
.dis {
  display: block;
  box-shadow: 0 2px 10px 0 #6d6d6d;
  width: 10ch;
  background: #fcfcfc;
  cursor: pointer;
  /*color: #889792;*/
  font-size: 16px;
  border-radius: 30px;
}
.dis:active {
  /*display: block;*/
  box-shadow: 0 1px 10px 0 #c9c9c9;
  /*width: 10ch;*/
  /*background: #FCFCFC;*/
  /*color: #889792;*/
  /*font-size: 16px;*/
  /*border-radius: 30px;*/
}
.dis:disabled {
  display: block;
  width: 10ch;
  background: var(--input-border);
  color: #889792;
  font-size: 16px;
  border-radius: 16px;
}
.red {
  display: block;
  width: 10ch;
  background: #ff3636;
  font-size: 16px;
  border-radius: 16px;
  cursor: pointer;
  border: none;
}
.red:hover {
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.5),
    0 0 20px rgba(255, 255, 255, 0.2);
  outline-color: rgba(255, 255, 255, 0);
  outline-offset: 15px;
  text-shadow: 1px 1px 2px #427388;
}
.icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin-bottom: 0;
  border-radius: 0;
}
.iconDefault {
  height: 20px;
  background: none;
  cursor: pointer;
}
.iconWithText {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 130px;
  height: 35px;
  border-radius: 20px;
  cursor: pointer;
}
.text {
  font-size: 18px;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}
