.paginationWrapper {
  display: flex;
  flex-direction: row;
  margin: 10px;
}
.paginator {
  display: flex;
  cursor: pointer;
  border-radius: 3px;
}
.pageNumber {
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
  cursor: pointer;
  /*text-align: center;*/
  padding: 0 3px;
  /*border: 1px solid grey;*/
  /*border-radius: 6px;*/
}

