.TableWrapper {
  display: flex;
  background: #678efe0a;
}
.returnBack {
  display: flex;
  width: fit-content;
  margin: 10px 0 10px 0;
  cursor: pointer;
}
.returnBack:hover {
  fill: var(--input-border-focus);
  color: var(--input-border-focus);
}
.headerBlock {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background: #f9f9fe;
  margin-bottom: 20px;
}
.wrapper {
  display: flex;
  justify-content: center;
}
.TableWrapper {
  display: flex;
  flex-direction: column;
  width: 1186px;
  min-width: 846px;
}
.tableBlock {
  margin-top: 10px;
  border: 3px solid #e8e8e8;
  border-radius: 4px;
  text-align: left;
}
.wrapper_header {
  background-color: #eeeeee;
  display: grid;
  grid-template-areas: "1 2 3 4 5";
  grid-template-columns: 25% 30% 15% 20%;
  gap: 1%;
  height: 48px;
  text-decoration: none;
  font-weight: 600;
  align-content: center;
}
.search {
  display: flex;
  flex-direction: column;
  padding: 5px 0;
}
.searchCardsHeader {
  font-weight: 500;
  padding-bottom: 10px;
}
.emptyBody {
  display: flex;
  flex-direction: column;
  height: 60vh;
  padding-top: 20vh;
  align-items: center;
  font-size: 16px;
  font-weight: 400;
}
.emptyBodyText {
  opacity: 50%;
  padding-bottom: 20px;
}
.circle {
  height: 18px;
  width: 18px;
  border-radius: 50%;
  /*text-align: center;*/
  /*vertical-align: middle;*/
  border: #0d0c0b solid 1px;
  cursor: pointer;
  /*font-size: 500%;*/
}
.cardName {
  display: flex;
  align-items: center;
}
.cardNameText {
  font-size: 22px;
  font-weight: 600;
  margin-right: 10px;
}
.pointBlock {
  padding: 4px;
  height: 10px;
  width: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.point {
  height: 2px;
  width: 2px;
  border-radius: 50%;
  background: #0d0c0b;
  /*background: #585858;*/
  /*padding: 2px;*/

  /*vertical-align: middle;*/
  /*border: #585858 solid 1px;*/
}
